.contato {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
}

.dados{
  padding: 0px;
  list-style: none;
}

.dados li {
  font-size: 1.2rem;
  font-family: Arial, Helvetica, sans-serif;
  margin-bottom: .8rem;
}

.dados li::before {
  content: '';
  display: inline-block;
  width: 15px;
  height: 4px;
  background-color: #ddd;
  margin-right: 10px;
  border-radius: 10px;
}